class Graph extends Component {
  constructor(props) {
    super(props);
    this.canvas = React.createRef();
    this.kind = props.kind;
    this.data = props.data;
    this.x0 = props.offsetX;
    this.y0 = props.offsetY;
    this.width = props.width
    this.height = props.height;
    this.scaleX = props.scaleX;
    this.scaleBars = this.data.initScaleBar || 0.1;
    this.minScaleBars = 0.001;
    this.animationFrames = 0;
  }

  componentDidMount() {
    if (!this.canvas.current)
      return;
    const that = this;
    function closure() {
      const maxFrames = 100;
      const h = draw(that);
      var needMore = false;
      if (h < 0.95) {
        needMore = true;
        that.animationFrames += 1;
        that.scaleBars *= 1 + (1 - Math.pow(h, 0.3)) / 3;
      }
      else if (h > 1) {
        if (that.minScaleBars > 0) {
          console.warn('scaleBars (' + that.scaleBars + ') is too large. It was originally ' + that.initScaleBars + '. It will be reset to ' + that.minScaleBars);
          needMore = true;
          that.animationFrames += 1;
          that.scaleBars = that.minScaleBars;
          that.minScaleBars = 0;
        }
        else console.error('Could not find the right value for scaleBars. Current value is ' + that.scaleBars + ', initial value was ' + that.initScaleBars);
      }
      if (needMore)
        if (that.animationFrames < maxFrames)
          window.requestAnimationFrame(closure);
        else
          console.warn('Needed to draw more, but maximum number of frames (' + maxFrames + ') are already drawn');
    }
    closure();
  }

  render = () => {
    return <canvas ref={this.canvas} />
  }
}

