export default class Benchmarks extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      address: props.address,
      failedMsg: undefined,
      data: undefined
    };
  }

  componentDidMount() {
    fetch(this.state.address)
      .then(response => {
        if (!response.ok)
          throw Error('./' + response.statusText);
        return response;
      })
      .then((r) => r.json())
      .then(js => {
        this.setState({
          isLoading: false,
          data: js
        })
      })
      .catch(e => this.setState({
        isLoading: false,
        failedMsg: e
      }))
  }

  render = () => {
    if (this.state.failedMsg)
      return (
        <Typography component='div' papers={this.state.papers} style={{ padding: 8 * 3 }}>
          <ErrorMessage msg={'Error in loading file.' + this.state.address} extra={this.state.failedMsg.toString()} />
        </Typography>);
    if (this.state.isLoading)
      return (
        <Typography component='div' papers={this.state.papers} style={{ padding: 8 * 3, width: '100%' }}>
          <Grid container justify='center' alignItems='center'>
            <Grid item><CircularProgress /></Grid>
          </Grid>
        </Typography>);
    const rows = this.state.data.results.map((result, index) => {
      // Make <span/>s for parameters (we don't make string, because we want to add non-breaking space as thousand separator)
      var params = [<span key='p-1'>Parameters:</span>];
      var size = [<span key='s-1'>Size:</span>];
      var first = true;
      for (var k in result.params) {
        params.push((first ? ' ' : ', ') + k + '=' + result.params[k]);
        first = false;
      }
      first = true;
      for (k in result.size) {
        const parts = result.size[k].replace(/\B(?=(\d{3})+(?!\d))/g, '&nbsp;').split('&nbsp;');
        const spaced = parts.map((n, i) => {
          if (i === 0) return n
          return <span key={'sp-' + i}>&#8239;{n}</span>
        });
        size.push(<span key={'s-' + k}>{first ? ' ' : ', '}{k}={spaced}</span>)
        first = false;
      }
      // If only one of 'params' or 'size' contains element or if none of them do then no need to say this is params, that is size
      if (params.length === 1 || size.length === 1) {
        params.shift();
        size.shift();
      }

      const s1 = draw({ kind: 'times', data: result.times }) / 100.0;
      const s2 = draw({ kind: 'samples', data: result.samples }) / 100.0;
      const w1 = s1 * 390.0;
      const w2 = s2 * 390.0;
      var description = result.description ? result.description.trim() : undefined;
      var note = result.note ? result.note.trim() : undefined;

      var appBar = undefined
      if (params.length > 0 || size.length > 0 || note) {
        var info = undefined;
        if (params && size) info = <Typography align='center' variant='caption'>{params}&emsp;&emsp;&emsp;{size}</Typography>;
        else if (params) info = <Typography align='center' variant='caption'>{params}</Typography>;
        else if (size) info = <Typography align='center' variant='caption'>{size}</Typography>;
        appBar = (
          <AppBar position='static' style={{ backgroundColor: 'rgb(32,39,42)' }}>
            {!info ? '' : info}
            {!note ? '' : <Typography align='justify' variant='caption' dangerouslySetInnerHTML={{ __html: note }} />}
          </AppBar>);
      }
      return (
        <Card key={index} style={{ marginTop: index = 0 ? '0px' : '10px', marginBottom: index = this.state.data.results.length ? '0px' : '10px' }}>
          <Grid container justify='space-evenly' >
            {!description ? '' :
              <Grid item key='desc' style={{ flexBasis: '100%' }}>
                <Typography align='justify' variant='body2' color='textPrimary' dangerouslySetInnerHTML={{ __html: description }} />
              </Grid>}
            <Grid item >
              <div style={{ marginLeft: '10pt', marginRight: '10pt', marginTop: '10pt' }}>
                <Graph offsetX={0} offsetY={0} width={w1} height={300} kind='times' data={result.times} scaleX={s1} style={{ lineHeight: 'normal' }} />
              </div>
            </Grid>
            <Grid item>
              <div style={{ marginLeft: '10pt', marginRight: '10pt', marginTop: '10pt' }}>
                <Graph offsetX={0} offsetY={0} width={w2} height={300} kind='samples' data={result.samples} scaleX={s2} style={{ lineHeight: 'normal' }} />
              </div>
            </Grid>
          </Grid>
          {!appBar ? '' : appBar}
        </Card>
      );
    });
    var description = this.state.data.description ? this.state.data.description.trim() : undefined;
    const cells = rows.map((row, index) => {
      return <Grid item key={index} zeroMinWidth={true}>{row}</Grid>;
    });
    if (description)
      cells.unshift(
        <Grid item xs={12} key='desc' zeroMinWidth={true}>
          <Typography align='justify' variant='body2' color='textPrimary' dangerouslySetInnerHTML={{ __html: description }} />
        </Grid>);
    return (
      <Grid
        container
        direction="row"
        justify="space-evenly"
        alignItems="flex-end"
      >
        {cells}
      </Grid>
    );


  };
}